import { createApp } from 'vue'
import App from './App.vue'
import i18n, { initializeTranslations } from './i18n'

// Uygulama olu≈ütur
const app = createApp(App)

// i18n'i ekle
app.use(i18n)

// √áevirileri y√ºkle ve uygulamayƒ± ba≈ülat
initializeTranslations().then(() => {
  app.mount('#app')
}).catch(error => {
  console.error('Failed to initialize translations, starting with defaults:', error)
  app.mount('#app')
})