  try {
    // Form verilerini API'ye g√∂nder
    const response = await ApiService.sendContactMessage({
      name: form.value.name,
      email: form.value.email,
      phone: form.value.phone || '',
      subject: form.value.subject,
      message: form.value.message
    })
    
    // Ba≈üarƒ±lƒ± mesajƒ± g√∂ster
    alert(response.message || t('contact.success'))
    
    // Form'u sƒ±fƒ±rla
    form.value = {
      name: '',
      email: '',
      phone: '',
      subject: '',
      message: ''
    }
    
  } catch (error) {
    // Hata mesajƒ±nƒ± g√∂ster
    alert(error.message || t('contact.error'))
  } finally {
    isSubmitting.value = false
  }
}

// Statistics Animation
const animateStats = () => {
  const statNumbers = document.querySelectorAll('.stat-number')
  
  statNumbers.forEach((stat) => {
    const target = parseInt(stat.getAttribute('data-target'))
    const increment = target / 100
    let current = 0
    
    const updateNumber = () => {
      if (current < target) {
        current += increment
        stat.textContent = Math.ceil(current)
        setTimeout(updateNumber, 20)
      } else {
        stat.textContent = target
      }
    }
    
    updateNumber()
  })
}

onMounted(async () => {
  // Initialize dark mode
  initDarkMode()
  
  // Add scroll listener for back to top button
  window.addEventListener('scroll', handleScroll)
  
  // Initialize lazy loading
  initLazyLoading()
  
  // Start hero slider
  startSlideShow()
  
  // API verilerini y√ºkle
  await loadApiData()
  
  // Loading animation
  setTimeout(() => {
    isLoading.value = false
    // Slider'ƒ± loading sonrasƒ± ba≈ülat
    startSlideShow()
  }, 2000)

  const observer = new IntersectionObserver(
    (entries) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          entry.target.classList.add('show-card')
        } else {
          entry.target.classList.remove('show-card')
        }
      })
    },
    { threshold: 0.2 }
  )
  
  // Statistics Observer
  const statsObserver = new IntersectionObserver(
    (entries) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          entry.target.classList.add('animate-stat')
          animateStats()
        }
      })
    },
    { threshold: 0.5 }
  )

  setTimeout(() => {
    projectCards.value.forEach((el) => {
      observer.observe(el)
    })
    
    // Observe statistics
    const statsSection = document.querySelector('.statistics-section')
    if (statsSection) {
      statsObserver.observe(statsSection)
    }
  }, 100)
})

onUnmounted(() => {
  // Clean up event listeners and observers
  window.removeEventListener('scroll', handleScroll)
  
  if (slideInterval.value) {
    clearInterval(slideInterval.value)
  }
  
  if (imageObserver.value) {
    imageObserver.value.disconnect()
  }
})
