/**
 * Aslan Energy Website - API Service
 * Backend API'leri ile ileti≈üim i√ßin service katmanƒ±
 */

import axios from 'axios'

// API base URL
const API_BASE_URL = 'http://localhost:8001/api'

// Axios instance olu≈ütur
const apiClient = axios.create({
  baseURL: API_BASE_URL,
  timeout: 10000,
  headers: {
    'Content-Type': 'application/json',
    'Accept': 'application/json'
  }
})

// Response interceptor - hatalarƒ± yakala
apiClient.interceptors.response.use(
  response => {
    console.log('API Response Success:', response)
    return response.data || response
  },
  error => {
    console.error('API Error:', error)
    console.error('Error Response:', error.response)
    console.error('Error Status:', error.response?.status)
    console.error('Error Data:', error.response?.data)
    
    if (error.response?.data?.error) {
      throw new Error(error.response.data.error)
    } else if (error.message) {
      throw new Error(error.message)
    } else {
      throw new Error('Bir hata olu≈ütu')
    }
  }
)

// API Service sƒ±nƒ±fƒ±
export class ApiService {
  
  // Hero Slides API
  static async getHeroSlides() {
    try {
      console.log('API Request: Getting hero slides...')
      const response = await apiClient.get('/hero-slides.php')
      console.log('Hero slides response:', response)
      return response.data || []
    } catch (error) {
      console.error('Hero slides getirilemedi:', error)
      console.error('Error details:', error.response?.data || error.message)
      return []
    }
  }

  // News API
  static async getNews(params = {}) {
    try {
      const response = await apiClient.get('/news.php', { params })
      return response.data || []
    } catch (error) {
      console.error('Haberler getirilemedi:', error)
      return []
    }
  }

  static async getNewsBySlug(slug) {
    try {
      const response = await apiClient.get('/news.php', { 
        params: { slug } 
      })
      return response.data || null
    } catch (error) {
      console.error('Haber detayƒ± getirilemedi:', error)
      return null
    }
  }

  // Statistics API
  static async getStatistics() {
    try {
      const response = await apiClient.get('/statistics.php')
      return response.data || []
    } catch (error) {
      console.error('ƒ∞statistikler getirilemedi:', error)
      return []
    }
  }

  // Contact API
  static async sendContactMessage(messageData) {
    try {
      const response = await apiClient.post('/contact.php', messageData)
      return response
    } catch (error) {
      console.error('Mesaj g√∂nderilemedi:', error)
      throw error
    }
  }

  // Services API
  static async getServices() {
    try {
      console.log('API Request: Getting services...')
      const response = await apiClient.get('/services.php')
      console.log('Services response:', response)
      return response.data || []
    } catch (error) {
      console.error('Hizmetler getirilemedi:', error)
      console.error('Error details:', error.response?.data || error.message)
      return []
    }
  }

  // Projects API (gelecekte eklenecek)
  static async getProjects() {
    try {
      const response = await apiClient.get('/projects.php')
      return response.data || []
    } catch (error) {
      console.error('Projeler getirilemedi:', error)
      return []
    }
  }

  // Translations API  
  static async getTranslations(locale = 'tr') {
    try {
      console.log('API Request: Getting translations for locale:', locale)
      const response = await apiClient.get('/translations.php', { 
        params: { locale } 
      })
      console.log('Translations full response:', response)
      
      // API response yapƒ±sƒ±nƒ± kontrol et
      if (response.success && response.data) {
        console.log('‚úì Using response.data:', response.data)
        return response.data
      } else if (response.data) {
        console.log('‚úì Using direct response.data:', response.data)
        return response.data
      } else {
        console.log('‚ö†Ô∏è No data in response:', response)
        return {}
      }
    } catch (error) {
      console.error('√áeviriler getirilemedi:', error)
      console.error('Error details:', error.response?.data || error.message)
      return {}
    }
  }
}

// Default export
export default ApiService