/**
 * Aslan Energy Website - Dynamic i18n Setup  
 * Veritabanƒ±ndan √ßevirileri y√ºkleyen dinamik i18n sistemi
 */

import { createI18n } from 'vue-i18n'
import ApiService from '../services/api.js'

// Varsayƒ±lan √ßeviriler (API ba≈üarƒ±sƒ±z olursa fallback)
const defaultMessages = {
  tr: {
    nav: {
      home: 'Anasayfa',
      about: 'Hakkƒ±mƒ±zda',
      services: 'Hizmetlerimiz',
      projects: 'Projelerimiz',
      news: 'Haberler',
      contact: 'ƒ∞leti≈üim'
    },
    common: {
      loading: 'Y√ºkleniyor...',
      darkMode: 'Karanlƒ±k Mod',
      lightMode: 'Aydƒ±nlƒ±k Mod'
    },
    contact: {
      success: 'Mesajƒ±nƒ±z ba≈üarƒ±yla g√∂nderildi!',
      error: 'Bir hata olu≈ütu. L√ºtfen tekrar deneyin.'
    }
  },
  en: {
    nav: {
      home: 'Home',
      about: 'About Us',
      services: 'Services',
      projects: 'Projects',
      news: 'News',
      contact: 'Contact'
    },
    common: {
      loading: 'Loading...',
      darkMode: 'Dark Mode',
      lightMode: 'Light Mode'
    },
    contact: {
      success: 'Your message has been sent successfully!',
      error: 'An error occurred. Please try again.'
    }
  }
}

// i18n instance olu≈ütur
const i18n = createI18n({
  legacy: false,
  locale: 'tr',
  fallbackLocale: 'en',
  messages: defaultMessages,
  silentTranslationWarn: true,
  silentFallbackWarn: true
})

// API'den √ßevirileri y√ºkle
export const loadTranslations = async (locale = 'tr') => {
  try {
    console.log(`Loading translations for ${locale}...`)
    const translations = await ApiService.getTranslations(locale)
    
    if (translations && Object.keys(translations).length > 0) {
      // API'den gelen √ßevirileri mevcut √ßevirilerle birle≈ütir
      const currentMessages = i18n.global.messages.value[locale] || {}
      const mergedMessages = { ...currentMessages, ...translations }
      
      i18n.global.setLocaleMessage(locale, mergedMessages)
      console.log(`‚úì Translations loaded for ${locale}:`, Object.keys(translations))
      return true
    } else {
      console.warn(`‚ö†Ô∏è No translations received for ${locale}, using defaults`)
      return false
    }
  } catch (error) {
    console.error(`‚ùå Error loading translations for ${locale}:`, error)
    return false
  }
}

// Dil deƒüi≈üimi fonksiyonu
export const changeLanguage = async (locale) => {
  try {
    // √ñnce √ßevirileri y√ºkle
    await loadTranslations(locale)
    
    // Dili deƒüi≈ütir
    i18n.global.locale.value = locale
    
    // Local storage'a kaydet
    localStorage.setItem('language', locale)
    
    console.log(`‚úì Language changed to ${locale}`)
    return true
  } catch (error) {
    console.error(`‚ùå Error changing language to ${locale}:`, error)
    return false
  }
}

// Ba≈ülangƒ±√ßta √ßevirileri y√ºkle
export const initializeTranslations = async () => {
  try {
    // Kaydedilmi≈ü dili al
    const savedLocale = localStorage.getItem('language') || 'tr'
    
    // Her iki dil i√ßin de √ßevirileri y√ºkle
    await Promise.all([
      loadTranslations('tr'),
      loadTranslations('en')
    ])
    
    // Kaydedilmi≈ü dili ayarla
    i18n.global.locale.value = savedLocale
    
    console.log('‚úì i18n initialized with API translations')
    return true
  } catch (error) {
    console.error('‚ùå Error initializing translations:', error)
    return false
  }
}

export default i18n